      flex-direction: column;
  height: 400px;
  width: 400px;
  min-width: 400px;
  min-height: 400px;
  background-color: #bbb;
}

.row {
  width: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
