  margin: 0 0 15px 0;
  height: 80px;
  background-color: #bbb;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
}

.color-swatch {
  border: 1px solid #999;
  width: 50px;
  height: 50px;
  margin: 0 2%;
}



